import { BasicColumn, FormSchema, useRender } from '@/components/Table';
import { defHttp } from '@/utils/http/axios';
import { useUserStore } from '/@/store/modules/user';

enum Api {
  getPausePage = '/po/inspection-pause/page',
  // deletePauseData = '/po/inspection-pause/delete',
  getPauseData = '/po/inspection-pause/get',
  updateData = '/po/inspection-pause/update',
}

// 查询
export function getPausePage(params) {
  return defHttp.get({ url: Api.getPausePage, params });
}
export function getPauseData(params) {
  return defHttp.get({ url: Api.getPauseData, params });
}
// 删除
// export function deletePause(id: string) {
//   return defHttp.delete({ url: Api.deletePauseData + '?id=' + id });
// }

export function updateDataPause(params) {
  return defHttp.put({ url: Api.updateData + '?id=' + params.id + '&status=' + params.bizStatus });
}
const statusColor = {
  0: '#dc0000', // 草稿
  1: '#338ed1', // 处理中
  2: '#30ca5a', // 通过
  3: '#929292', // 不通过
  4: '#929292', // 已取消
  5: '#929292', // 已终止
  6: '#fda941', // 退回/驳回
};
export const columns: BasicColumn[] = [
  { title: '单据编号', dataIndex: 'billCode', width: 160,sorter: true,resizable: true },
  { title: '所属公司', dataIndex: 'stationOrganName',align: 'left',headAlign:'center',
   width: 160,sorter: true,resizable: true,ifShow:useUserStore().isMultiOrganization },
  { title: '电站名称',align: 'left',headAlign:'center', dataIndex: 'stationName', width: 160,sorter: true,resizable: true },
  { title: '巡检区域', dataIndex: 'inspectionAreaTypeText', width: 160,sorter: true,resizable: true },
  { title: '暂停开始时间', dataIndex: 'startDate', width: 120,sorter: true,resizable: true },
  { title: '暂停结束时间', dataIndex: 'endDate', width: 120,sorter: true,resizable: true },
  {
    title: '审批状态',
    dataIndex: 'statusText',
    width: 100,sorter: true,resizable: true,
    customRender: ({ text, record }) => {
      return useRender.renderTag(text, statusColor[record.status]);
    },
  },
  { title: '创建人', dataIndex: 'personMemberName', width: 100,sorter: true,resizable: true },
  { title: '创建时间', dataIndex: 'fillinDate',sorter: true,resizable: true },
];

